// 测试mock model
import BaseModel from './base.model';

class TestModel extends BaseModel {
	constructor(props) {
		super(props);
		this.method = 'get';
		this.baseUrl = '/';
		this.showAjaxLoading = false;
		this.url = 'testApis/search';
	}
}

export default TestModel;


