
import Vue from 'vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import { mapState, mapActions } from 'vuex'

import { isLogined } from '@/service/checkUserLogin'
import { SendCodeModel, LoginMobileModel } from '@/model/com.model'
import storage from '@/localstorage/conf'
import * as Acts from '@/store/consts'

let infoKey = storage.user;

export default {
name: 'demo-login',
data: () => {
return {
tel: '',
code: ''
}
},
computed: {
...mapState(['User'])
},
created() {
this.SendCodeModel = new SendCodeModel();
this.LoginMobileModel = new LoginMobileModel();
this.SendCodeModel.setUrl();
this.LoginMobileModel.setUrl();
},
methods: {

setLocalUserInfo(data) {
Vue.ls.set(infoKey, data);
},
removeLocalUserInfo() {
Vue.ls.remove(infoKey);
},
evt_sendCode() {
if (this.tel) {
this.SendCodeModel
.setParams({
mobile: this.tel
})
.go()
.then(res => {
this.$vux.toast('验证码已发送');

})
.catch(err => {
console.log(err);
});
}
},
evt_doLogin() {
this.LoginMobileModel
.setParams({
mobile: this.tel,
code: this.code
})
.go()
.then(res => {

this[Acts.SET_USER_INFO](res.data);

this.setLocalUserInfo(res.data);
})
.catch(err => {
this.$vux.toast.text(err.msg);
})
},
evt_logout() {
this[Acts.SET_USER_INFO]({id: null, mobile: ''});
this.removeLocalUserInfo();
},
evt_checkLogin() {
if (isLogined()) {
this.$vux.toast.text('已登录');
} else {
this.$vux.toast.show({
text: '未登录',
type: 'text',
position: 'top',
isShowMask: true,
onHide: () => {
this.$router.push({
name: 'home_login',
query: {
from: this.$route.name
}
})
}
});
}
},
...mapActions([Acts.SET_USER_INFO])
},
components: {
XButton,
XInput
}
}



