import BaseModel from './base.model'

export class SendCodeModel extends BaseModel {
	constructor(props) {
		super(props);
		this.url = 'login/loginSMS';
		this.desc = '发送短信验证码';
	}
}

export class LoginMobileModel extends BaseModel {
	constructor(props) {
		super(props);
		this.url = 'Login/Login';
		this.desc = '手机登录';
	}
}

export class QueryCityAreaModel extends BaseModel {
	constructor(props) {
		super(props);
		this.url = 'city/list';
		this.showAjaxLoading = false;
		this.desc = '获取省市区街道列表';
	}
}


