import Vue from 'vue'
import storage from '@/localstorage/conf'
import router from '@/entry/index'
/*
	检查用户登录的路由钩子
	用户未登录会跳转登录页面
*/
export const userLoginHook = (to, from, next) => {
	if (isLogined()) {
		next();
	} else {
		// 直接跳进来的, 不保留历史记录
		if (_.isNull(from.name)) {
			router.replace('/login');
		} else {
			// 保留回退跳转的历史记录
			next(vm => {
				vm.$router.replace('/login');
			});
		}
	}
}

export const isLogined = () => {
	let uInfo = Vue.ls.get(storage.user);
	if (uInfo && uInfo.id) {
		return true;
	}
	return false;
}


