// 本地存储
const storage = {
	namespace: 'ZZL_VUE',
}

// immutable
Object.defineProperty(storage, 'user', {
	get: () => {
		return `${storage.namespace}_USER`
	}
});

Object.defineProperty(storage, 'test', {
	get: () => {
		return `${storage.namespace}_TEST`
	}
});

export default storage;


