
import XInput from 'vux/src/components/x-input/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import TestModel from '@/model/test.model'

export default {
name: 'demo-ajax',
data: () => {
return {
email: '',
isLoading: false,
debounce: 300,
searchList: []
}
},
created() {
this.TestModel = new TestModel();
},
methods: {
evt_fetchKeyWords(v) {
if (v) {
this.isLoading = true;
this.TestModel
.setQuerys({query: v, from: this.$route.name})
.setParams({page: 1})
.go()
.then(rst => {
this.isLoading = false;
this.searchList = rst.list;
})
.catch(err => {
this.isLoading = false;
this.searchList = [];
this.$vux.toast.text(err.code);
});
} else {
this.isLoading = false;
this.searchList = [];
}
}
},
components: {
XInput,
Spinner,
Group,
Cell
}
}



